//frontend/pages/admin/message.js
import { useEffect, useState } from "react";
import api from "../../../utils/api";
import { useRouter } from "next/router";

export default function AdminMessages() {
  const [users, setUsers] = useState([]);
  const router = useRouter();

  useEffect(() => {
    loadUsers();
  }, []);

  const loadUsers = async () => {
    try {
      const token = localStorage.getItem("token"); // admin token
      if (!token) return alert("Please login as admin first.");

      const res = await api.get("/admin/message-users", {
        headers: { Authorization: `Bearer ${token}` },
      });

      setUsers(res.data);
    } catch (err) {
      console.error(err);
      alert("Failed to load users. Make sure you are admin.");
    }
  };

  return (
    <div style={{ padding: "20px" }}>
      <h2>User List (Token Only)</h2>
      {users.length === 0 && <p>No users yet</p>}
      {users.map((u) => (
        <div
          key={u._id}
          onClick={() => router.push(`/admin/messages/${u._id}`)}
          style={{
            cursor: "pointer",
            marginTop: "10px",
            padding: "8px",
            border: "1px solid #ccc",
            borderRadius: "5px",
          }}
        >
          ðŸ”‘ {u._id}
        </div>
      ))}
    </div>
  );
}
